  display: flex;
  flex-wrap: wrap;
  .dib{
    background: #ffffff;
    margin: 10px;
    text-align: center;
    width: 160px;
    padding: 10px;
    border-radius: 5px;
    .icon{
      font-size: 30px;
    }
    .name{
      font-size: 16px;
      font-weight: bold;
      color: #000000;
      padding: 8px;
    }
    .code-name{
      color: #aaaaaa;
    }
  }
}
